body {
  background-color: var(--body-bg-color);
  transition: all 0.3s ease;
}

.main-div {
  width: 500px;
  height: 350px;
  background-color: beige;
  margin: 90px auto;
  padding-top: 0px;
}

.h1 {
  text-align: center;
  margin-bottom: 20px;
  transition: all 0.3s ease;
}

.p {
  font-size: 19px;
  height: 25px;
  margin-left: 320px;
  margin-bottom: 30px;
  transition: all 0.3s ease;
}

.tweet {
  text-decoration: none;
  color: white;
  position: relative;
  top: 3px;
  height: 25px;
  padding: 8px;
  border-radius: 3px;
  margin-right: 200px;
  margin-left: 100px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.button {
  border: none;
  height: 38px;
  color: white;
  border-radius: 3px;
  cursor: pointer;
  transition: all 0.3s ease;
}/*# sourceMappingURL=App.css.map */